body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 15px;
  font-family: Manrope, sans-serif;
  color: #292929;
  text-align: center;
}

h1 {
  font-family: Manrope, sans-serif;
  font-size: 26px;
  font-weight: 400;
  line-height: 1.2;
  color: #292929;
  margin-left: auto;
  margin-right: auto;
}

#spinner-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  margin: 15px auto;
}

#spinner {
  position: relative;
  display: block;
  width: 65px;
  height: 65px;
  border-top: 4px solid #256bd1;
  border-right: 4px solid #256bd1;
  border-bottom: 4px solid #256bd1;
  border-left: 4px solid transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

#spinner::before {
  position: absolute;
  top: 6px;
  left: 6px;
  width: 4px;
  height: 4px;
  content: "";
  background-color: #256bd1;
  border-radius: 50%;
}

#spinner::after {
  position: absolute;
  bottom: 6px;
  left: 6px;
  width: 4px;
  height: 4px;
  content: "";
  background-color: #256bd1;
  border-radius: 50%;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.home-btn {
  display: inline-block;
  background-color: #fedd0b;
  border: 2px solid #fedd0b;
  color: #292929;
  border-radius: 10px;
  padding: 10px 20px;
  min-width: 200px;
  margin: 30px auto;
  text-decoration: none;
}

.home-btn:hover, .home-btn:focus {
  background-color: #fff;
  color: #292929;
}

